@font-face {
  font-family: "Noir Pro";
  src: url("./assets/fonts/NoirPro-Light.ttf") format("ttf");
  font-weight: 200;
}
@font-face {
  font-family: "Noir Pro";
  src: url("./assets/fonts/NoirPro-Regular.ttf") format("ttf");
  font-weight: 400;
}
@font-face {
  font-family: "Noir Pro";
  src: url("./assets/fonts/NoirPro-Medium.ttf") format("ttf");
  font-weight: 600;
}
@font-face {
  font-family: "Noir Pro";
  src: url("./assets/fonts/NoirPro-Bold.ttf") format("ttf");
  font-weight: 800;
}
@font-face {
  font-family: "Noir Pro";
  src: url("./assets/fonts/NoirPro-Heavy.ttf") format("ttf");
  font-weight: 900;
}
* {
  font-family: "Noir Pro", sans-serif;
}
.container,
.container-fluid,
.container-sm,
.container-md,
.container-lg,
.container-xl,
.container-xxl {
  width: 100%;
  padding-right: 1rem;
  padding-left: 1rem;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 576px) {
  .container,
  .container-sm {
    max-width: 540px;
  }
}

@media (min-width: 768px) {
  .container,
  .container-sm,
  .container-md {
    max-width: 720px;
  }
}

@media (min-width: 992px) {
  .container,
  .container-sm,
  .container-md,
  .container-lg {
    max-width: 960px;
  }
}

@media (min-width: 1200px) {
  .container,
  .container-sm,
  .container-md,
  .container-lg,
  .container-xl {
    max-width: 1140px;
  }
}

