.img-fluid {
  max-width: 100%;
  height: auto;
}
.d-flex {
  display: flex !important;
}
.ant-layout {
  background: #f5f5f5;
}
h1,
h1.ant-typography,
.ant-typography h1 {
  font-size: 3.75rem;
  line-height: 1;
  font-weight: bold;
}
h2,
h2.ant-typography,
.ant-typography h2 {
  font-size: 3rem;
  line-height: 1;
  font-weight: bold;
  // margin-bottom: 0;
}
div.ant-typography,
.ant-typography p {
  font-size: 1.875rem;
}
.ant-btn-lg {
  font-size: 18px;
  height: 45px;
  border-radius: 4px;
  background: #fb8c00;
  font-weight: 600;
  border: none;
  &:hover{
    background: #ffa73c;
  }
}

.text-center {
  text-align: center;
}
/* ul{
  list-style-image: url("./assets/check.png");
} */
.check-list {
  // padding-left: 40px;
  li {
    list-style: none;
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    font-size: 1.25rem;
  }
  li:before {
    content: "";
    display: inline-block;
    position: absolute;
    height: 25px;
    width: 25px;
    margin-right: 20px;
    left: -35px;
    top: 2px;
    background-image: url("./assets/check.png");
    font-size: 20px;
  }
}

.download-btn {
  display: inline-flex;
  width: 155px;
  height: 55px;
  background-color: #000;
  border-radius: 5px;
  margin-right: 40px;
  padding: 5px 10px;
  margin-top: 10px;
  position: relative;
  &::after {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;

    top: 0;
    left: 0;

    z-index: 1;
  }
  &--apple {
    &::after {
      background: url("./assets/apple-btn.png") no-repeat center/contain;
      background-size: 135px;
    }
  }
  &--gplay {
    &::after {
      background: url("./assets/google-btn.png") no-repeat center/contain;
      background-size: 130px;
    }
  }
}
.phone-number {
  font-size: 1.125rem;
  line-height: 1;
}
.ant-input {
  height: 100%;
  font-size: 1.125rem;
  border-radius: 5px;
  border: none;
  color: #000;
  padding-right: 20px;
  padding-left: 20px;
  height: 45px;
  margin-bottom: 15px;
  &::placeholder {
    color: #333;
  }
}
